import {Component, EventEmitter, Input, Output} from '@angular/core';

@Component({
  selector: 'app-bootstrap-button',
  templateUrl: './bootstrap-button.component.html',
  styleUrls: ['./bootstrap-button.component.css'],
})
export default class BootstrapButtonComponent {
  @Input() color: string;
  @Input() label: string;
  @Input() outline: boolean;
  @Input() disabled: boolean;
  @Input() linkButton: boolean;
  @Input() url: string;

  @Output() onClick = new EventEmitter<Event>();

  getColor(): string {
    return (this.color || 'default').toLowerCase();
  }

  isDisabled(): boolean {
    return this.disabled;
  }

  isLink(): boolean {
    return this.linkButton;
  }

  getButtonClass() {
    if (this.outline) {
      return `btn-outline-${this.getColor()}`;
    }
    return `btn-${this.getColor()}`;
  }
}
