import {Component, EventEmitter, Input, Output} from '@angular/core';

@Component({
  selector: 'app-outline-input-style',
  templateUrl: './outline-input-style.component.html',
  styleUrls: ['./outline-input-style.component.css'],
})
export default class OutlineInputStyleComponent {
  @Input() label: string;
  @Input() type: string;
  @Input() options: string[];

  @Output() onChange = new EventEmitter<any>();

  isInput() {
    return this.type === 'Input';
  }

  isSelect() {
    return this.type === 'Select';
  }

  isTextarea() {
    return this.type === 'Textarea';
  }
}
