import {Component, EventEmitter, Input, Output} from '@angular/core';
import {FormControl} from '@angular/forms';

export const SIMPLE_PLACEHOLDER = "Simple Placeholder";
export const PLACEHOLDER_WITH_LABEL = "Both a label and a placeholder";
export const FANCY_LABEL = "Fancy Label";

@Component({
  selector: 'app-form-field-with-label',
  templateUrl: './form-field-with-label.component.html',
  styleUrls: ['./form-field-with-label.component.css'],
})
export default class FormfieldwithlabelComponent {
  @Input() label: string;
  @Input() placeholder: string;
  @Input() type: string;
  @Input() icon: string;
  @Input() options: string[];

  @Output() onChange = new EventEmitter<any>();

  hideRequiredControl = new FormControl(false);
  floatLabelControl = new FormControl('auto');

  isSimplePlaceholder() {
    return this.type === SIMPLE_PLACEHOLDER;
  }

  isPlaceholderWithLabel() {
    return this.type === PLACEHOLDER_WITH_LABEL;
  }

  isFancyLabel() {
    return this.type === FANCY_LABEL;
  }

  getOptions() {
    return this.options || [];
  }
}
