import {Component, EventEmitter, Input, Output} from '@angular/core';

@Component({
  selector: 'app-fill-input-style',
  templateUrl: './fill-input-style.component.html',
  styleUrls: ['./fill-input-style.component.css'],
})
export default class FillInputStyleComponent {
  @Input() label: string;
  @Input() type: string;
  @Input() options: string[];

  @Output() onChange = new EventEmitter<any>();

  isInput() {
    return this.type === 'Input';
  }

  isSelect() {
    return this.type === 'Select';
  }

  isTextarea() {
    return this.type === 'Textarea';
  }
}
