import {Component, EventEmitter, Input, Output} from '@angular/core';
import {ThemePalette} from "@angular/material/core";

@Component({
  selector: 'app-flat-button',
  templateUrl: './flat-button.component.html',
  styleUrls: ['./flat-button.component.css'],
})
export default class FlatButtonComponent {
  @Input() color: string;
  @Input() label: string;
  @Input() disabled: boolean;
  @Input() linkButton: boolean;
  @Input() url: string;

  @Output() onClick = new EventEmitter<Event>();

  getColor(): ThemePalette {
    return (this.color || 'basic').toLowerCase() as "primary" | "accent" | "warn";
  }

  isDisabled(): boolean {
    return this.disabled;
  }

  isLink(): boolean {
    return this.linkButton;
  }
}
